.words {
  font-size: 1.2rem;
  margin-left: auto;
  margin-right: auto;
  margin-bottom: 1.2rem;
  max-width: 500px;
}

.title {
  margin: 0.3rem;
  font-size: 2rem;
}

.icon {
  padding: 1rem;
  padding-top: .5rem;
  font-size: 2.2rem;
}

.abCard {
  box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2);
  border-radius: 15px;
  /* 5px rounded corners */
  /* margin-left: auto;
  margin-right: auto; */
  width: 400px;
  display: inline-block;
  padding: 10px;
  margin-left: 20px;
  margin-right: 20px;
  margin-top: 20px;
}

@media (min-width: 700px) {
  .abCard {
    width: 100%;
  }
}

