.container {
  margin: 0.1rem;
  display: inline-block;
  justify-content: center;
  align-items: center;
}

.title {
  font-size: 2rem;
}

.card {
  background-color: rgb(0, 0, 49, 0.8);
  box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2);
  border-radius: 15px;
  /* 5px rounded corners */
  /* margin-left: auto;
  margin-right: auto; */
  width: 23rem;
  height: 30rem;
  display: inline-block;
  padding: 10px;
  margin: 20px;
  margin-top: .5rem;
  /* justify-content: center;
  align-items: center; */
}

.scrnsht {
  max-height: 8rem;
  border-radius: 10px;
  border-style: solid;
  border-width: .2rem;
}

.desc {
  width: 18rem;
  display: inline-block;
  justify-content: center;
}