.social {
  transition: transform .5s;
  font-size: 2rem;
  padding-left: 1rem;
  padding-right: 1rem;
}

.social:hover {
  transform: translateY(-3px);
}

.homeText {
  cursor: default;
}

.homeText:hover {
  transform: scale(1.01);
}

.name {
  margin: 0.5rem;
  margin-top: 2rem;
  font-size: 2.2rem;
}

.subName {
  margin-top: 0.5rem;
  font-size: 1.2rem;
}

.homeCard {
  box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2);
  border-radius: 15px;
  /* 5px rounded corners */
  /* margin-left: auto;
  margin-right: auto; */
  width: auto;
  display: inline-block;
  padding: 25px;
  margin-top: 10px;
}

@media (min-height: 380px) {
  .homeCard {
    margin-top: 25%;
  }
}